import { useState } from 'react';
import './App.css';

function App() {
  const [saldo, setSaldo] = useState(0)
  const updateSaldo = (valueChange) =>{
    let result = true
    if(valueChange < 0) result = window.confirm("Apakah anda ingin menarik saldo?")
    if(result) {
      if((saldo + valueChange) < 0) alert("Saldo anda tidak mencukupi")
      else setSaldo(saldo + valueChange)
    }
  }
  return (
    <div className="App">
      <h2>Hello Everyone</h2>
      <p>Saldo anda adalah : Rp. {parseFloat(saldo).toLocaleString()}</p>
      <hr/>
      <p>Apakah mau tambah saldo?</p>
      <button onClick={()=>updateSaldo(1000)}>Tambah Rp. 1.000</button>
      <button onClick={()=>updateSaldo(5000)}>Tambah Rp. 5.000</button>
      <hr/>
      <p>Apakah mau tarik saldo?</p>
      <button onClick={()=>updateSaldo(-1000)}>Tarik Rp. 1.000</button>
    </div>
  );
}

export default App;
